@import url('https://fonts.googleapis.com/css2?family=Comic+Neue&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Comic Neue', cursive;
    // position: absolute;
}

html,
body {
    font-size: 10px;
    background: ghostwhite;
}

.app__base {
    display: flex;
    justify-content: center;
}

.form__card {
    height: 600px;
}

form {
    margin-top: 23%;
    width: 650px;
    padding: 0.4em 1.4em 2em;
    font-size: 1.75em;
    border-radius: 6px;
    box-shadow: 0 2.8px 4px rgba(0, 0, 0, 0.012),
        0 6.2px 12.5px rgba(0, 0, 0, 0.034), 0 12px 21.2px rgba(0, 0, 0, 0.035),
        0 24.3px 42.2px rgba(0, 0, 0, 0.043),
        0 38.2px 64.2px rgba(0, 0, 0, 0.062),
        0 62.3px 100px rgba(0, 0, 0, 0.012);
}

.form__control {
    width: 100%;
    height: 36px;
    border-radius: 4px;
    outline: none;
    border: none;
    padding: .1em .8em;
    font-size: 1.2em;
}

.flex {
    width: 100%;
    display: flex;
}

.flex__item {   
    width: 100%;
}

.select {
    width: 55%;
    height: 2em;
    background: none;
    outline: none;
    font-size: 1em;
    border-radius: 0.2em;
}

.form__group {
    padding: 0.2em 1em 0.8em;
}

.cvv {
    width: 100%;
    height: 2.6em;
    border-radius: 3px;
    border: none;
    outline: none;
    padding: .1em .6em;
    margin-left: -4%;
    &::placeholder {
        padding: .1em .3em;   
        color: black; 
    }
}
    
